@font-face {
    font-family: 'Dosis';
    src: url(/deadcellsmap/static/media/Dosis-VariableFont_wght.d89d7b6e.ttf);
}
body {
    background-color: #161a33;
    

    font-family: 'Dosis';
    margin:0;
    scrollbar-width: none !important;
    overflow:scroll;
}
* {
    scrollbar-width: none !important;
}
#settingsButton {
    width: 5vh;
    height:5vh;
}
#archerMajorContainer div::-webkit-scrollbar {
    display: none;
}
#container {
    height: 400vh;
}
.mapTile {
    scrollbar-width: none !important;
    display:flex;
    flex-direction: column;
    position:absolute;

    width:15%;
    height:5%;

    margin:10px;
    text-align: center;
    
    border:#6e3433 solid 5px;
    border-radius: 7px;
    background-color: #34aeda;
    
    
}
.edgeLabel {
    color: white;
    display:flex;
    align-items: center;
    
    background-color:#161a33dd;
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
}
.edgeLabel * {
    
}
@media (orientation: portrait) {
    .mapTile {
        font-size: 1vh;
    }
}
@media (orientation: landscape) {
    .mapTile {
        font-size: 1vw;
    }
}
#background {
    width:100vw;
    height:100vh;
    position:fixed;

    background-image: url("https://chmtv.github.io/deadcellsmap/titleScreen.png");
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
}
#archerMajorContainer, #container, #archerMajorContainer > div, #archerMajorContainer > svg{
    height:400vh !important;
    overflow:visible;
}
path:hover, g:hover {
    stroke: red;
    fill: red;
}
g path:hover {
    stroke: red !important;
}
g {
    stroke-width: 2px !important;
}
svg, svg *{
    pointer-events: all !important;
}

.mapImgContainer {
    width: 100%;
    height:100%;
    background-size:cover;
    background-repeat: no-repeat;
}
.mapImgContainer img {

}

.mapName {
    height:30%;
    display:flex;
    align-items: center;
    justify-content: center;
    padding:5px;
}
.mapName div {
    padding:0.5vw;
}
.mapDetails {
    background-color: #34aeda;
    height:20%;
    display:flex;
    justify-content: center;
    align-items: center;
    padding:5px;
}
.mapDetails div {
    display:flex;
    justify-content: center;
    align-items: center;
}
.mapDetails img {
    width:24px;
    height:24px;
}







section {
    display:flex;
}
section input {
    display:none;
}
button {
    background: none;
    border: none;
    color: white;
}
button img{
    width:100%;
    height:100%;
}
/* NIE DZIALA W CHUJ OGOLEM */
#settingsButton {
    position:fixed;
    left: 95vw;
    top: 21vh;
    z-index: 3;
    transition: all 0.2s;
}


#menu {
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    position: fixed;
    width:96vw;
    
    overflow: hidden;
    z-index: 2;
    margin-top: 2vh;
    margin-left:2vw;

    /* From https://css.glass */
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    background-color:#161a3399;
    -webkit-backdrop-filter: blur(5px);

    transition: all 0.2s;
}
.settingsMenuOpen {
    height: 20vh;
    outline: #7c362e solid 2px;
    border: #541e12 solid 2px;
}
.settingsMenuClosed {
    height: 0;
    border:none;
    outline: none;
}
.settingsButtonClosed {
    top: 2vh !important;
}


label {
    display:flex;
    justify-content:center;
    align-items:center;

    width: 5vh;
    height:5vh;
    color: white;
    background-color: #262a43;
    background-size: cover;
}
label, section img {
    outline: #7c362e solid 2px;
    border: #541e12 solid 2px;
}
#dlc label {
    margin:5px;
    width:8vw;
    height:4vw;
    -webkit-filter: grayscale(90%);
            filter: grayscale(90%);
}
#dlc input[type="checkbox"]:checked+label {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}
#dlc {
    
}
section img {
    height:5vh;
}
section {
    display:flex;
    align-items: center;
    justify-content: center;
    justify-items: center;
    height: 33%;
    width:33vw;
}

input[type="radio"]:checked+label, input[type="checkbox"]:checked+label, input[type="checkbox"]:checked+label img {
    background-color: #541e12;
    opacity:1;
}
input[type="radio"]+label, input[type="checkbox"]+label, input[type="checkbox"]+label {
    opacity: 0.75;
}
